import Vue from 'vue'
import Vuex from 'vuex'
import common from './modules/common'
import repair from './modules/repair'
import getters from './getters'
Vue.use(Vuex)

const store = new Vuex.Store({
    modules: {
        common,
        repair
    },
    getters
});

export default store



