import Vue from 'vue'
import Router from 'vue-router'
import * as _ from './hook'
/* author */
const author = () =>
	import('views/author')

/* lottery */
const lottery = () =>
	import('views/lottery')
const newUser = () =>
	import('views/lottery/newUser')

/* 404 */
const nofound = () =>
	import('views/nofound')
/* repairs */
/* my-repairs */
const selectBuilding = () =>
	import('views/repairs/my-repairs/selectBuilding')
const repair = () =>
	import('views/repairs/my-repairs/repair')
const repairResult = () =>
	import('views/repairs/my-repairs/repairResult')

/* repairs-record */
const repairsRecord = () =>
	import('views/repairs/repairs-record')
const repairDetail = () =>
	import('views/repairs/repairs-record/repairDetail')
const personInfo = () =>
	import('views/repairs/repairs-record/personInfo')
const evaluate = () =>
	import('views/repairs/repairs-record/evaluate')

/* my-evaluate */
const myEvaluate = () =>
	import('views/repairs/my-evaluate')

/* spitslot */
const spitslot = () =>
	import('views/spitslot')

/* 朵朵报修 */
const ddRepair = () =>
	import('views/duoduoRepair')

/* realize */
const realize = () =>
	import('views/realize')

const devices = () =>
	import('views/devices')

const ydgz = () =>
	import('views/devices/ydgz')
const questionsurvey = () =>
	import('views/questionsurvey')
//介绍
const introduce = () =>
	import('views/questionsurvey/children/introduce')
//用户信息
const userInfo = () =>
	import('views/questionsurvey/children/userInfo')
//第一部分
const firstpart = () =>
	import('views/questionsurvey/children/firstpart')
//第二部分
const secondpart = () =>
	import('views/questionsurvey/children/secondpart')
//测试结果
const questionresult = () =>
	import('views/questionsurvey/children/result')
//第一部分测试结果
const questionreport = () =>
	import('views/questionsurvey/children/report1')
//第二部分测试结果
const questionreport2 = () =>
	import('views/questionsurvey/children/report2')

Vue.use(Router)

export const constantRouterMap = [{
		path: '/author',
		name: 'author',
		component: author
	},
	{
		path: '/lottery',
		name: 'lottery',
		component: lottery
	},
	{
		path: '/newUser',
		name: 'newUser',
		component: newUser
	},
	{
		path: '/nofound',
		name: 'nofound',
		component: nofound
	},
	{
		path: '/selectBuilding',
		name: 'selectBuilding',
		component: selectBuilding
	},
	{
		path: '/repair',
		name: 'repair',
		component: repair
	},
	{
		path: '/repairResult',
		name: 'repairResult',
		component: repairResult
	},
	{
		path: '/repairsRecord',
		name: 'repairsRecord',
		component: repairsRecord
	},
	{
		path: '/repairDetail',
		name: 'repairDetail',
		component: repairDetail
	},
	{
		path: '/personInfo',
		name: 'personInfo',
		component: personInfo
	},
	{
		path: '/evaluate',
		name: 'evaluate',
		component: evaluate
	},
	{
		path: '/myEvaluate',
		name: 'myEvaluate',
		component: myEvaluate
	},
	{
		path: '/spitslot',
		name: 'spitslot',
		component: spitslot
	},
	{
		path: '/realize',
		name: 'realize',
		component: realize
	},
	{
		path: '/ddRepair',
		name: 'ddRepair',
		component: ddRepair
	},
	{
		path: '/devices',
		name: 'devices',
		component: devices
	},
	{
		path: '/ydgz',
		name: 'ydgz',
		component: ydgz
	},
	{
		path: '/questionsurvey',
		component: questionsurvey,
		children: [{
				path: '',
				component: introduce,
				beforeEnter: _.questionsurvey, //在进入页面之前，判断进入到哪里
			},
			{
				path: 'introduce',
				component: introduce
			}, //介绍页面
			{
				path: 'userInfo',
				name: 'userInfo',
				component: userInfo
			}, //用户信息
			{
				path: 'firstpart',
				name: 'firstpart',
				component: firstpart
			}, //第一页的问题
			{
				path: 'secondpart',
				name: 'secondpart',
				component: secondpart
			}, //第二页的问题
			{
				path: 'questionresult',
				name: 'questionresult',
				component: questionresult
			},
			{
				path: 'questionreport',
				name: 'questionreport',
				component: questionreport
			},
			{
				path: 'questionreport2',
				name: 'questionreport2',
				component: questionreport2
			},
		]
	},
]

export default new Router({
	mode: 'history', //后端支持可开
	scrollBehavior: () => ({
		y: 0
	}),
	routes: constantRouterMap
})


