import tools from 'utils/tools'
import {
    weChatConfig
} from 'api/global'
import { getAccessToken, getJsapiTicket, getUploadToken } from 'api/other'

const common = {
    state: {
        openId: '',
        access_token: '',
        jsapi_ticket: '',
        upload_token: ''
    },
    mutations: {
        SET_ACCESS_TOKEN: (state, access_token) => {
            state.access_token = access_token
            tools.setCookie('access_token', access_token)
        },
        SET_JSAPI_TICKET: (state, jsapi_ticket) => {
            state.jsapi_ticket = jsapi_ticket
            tools.setCookie('jsapi_ticket', jsapi_ticket)
        },
        SET_OPENID: (state, openId) => {
            state.openId = openId
            tools.setCookie('openId', openId)
        },
        SET_UPLOAD_TOKEN: (state, upload_token) => {
            state.upload_token = upload_token
            tools.setCookie('upload_token', upload_token)
        }
    },
    actions: {
        SetCommon: ({ commit }, fullPath) => {
            getUploadToken().then(res => {
            	//获取七牛上传的token
                if (res.data.Result === 'success') {
                    commit('SET_UPLOAD_TOKEN', res.data.upload_token)
                }
            })
            getAccessToken().then(res => {
            	//获取微信的accesstoke
                if (res.data.Result === 'success') {
                    commit('SET_ACCESS_TOKEN', res.data.acesstoken)
                    getJsapiTicket().then(res => {
                        if (res.data.Result === 'success') {
                        	//获取ticket
                            commit('SET_JSAPI_TICKET', res.data.ticket)
                            //利用当前的路径，获取权限
                            weChatConfig(fullPath)
                        }
                    }).catch(error => {
                        console.log(error)
                    })
                }
            }).catch(error => {
                console.log(error)
            })
        },
        SetOpenId: ({ commit }, openId) => {
            commit('SET_OPENID', openId)
        }
    }
}

export default common



