import fetch from 'utils/sagaCloudFetch'
//获取access_token
export function getAccessToken() {
  let data = {}
  return fetch({
    method: 'POST',
    url: `/accesstoken`,
    data
  })
}

//获取jsapi_ticket
export function getJsapiTicket() {
  let data = {}
  return fetch({
    method: 'POST',
    url: `/getTicket`,
    data
  })
}

//七牛获取uploadToken
export function getUploadToken() {
  let data = {}
  return fetch({
    method: 'POST',
    url: `/getUploadToken`,
    data
  })
}


