import fetch from 'utils/sagaCloudFetch'
import tools from 'utils/tools'
const openId = tools.getCookie('openId')
  // const openId = 'oj_Zcv7W88dU30bjlPyS9a_ynsNU'
  // console.log(openId)

export function getObjectInfoByMd5(md5) {
  let data = {
    md5
  }
  return fetch({
    method: 'POST',
    url: `/getObjectInfoByMd5`,
    data
  })
}

//获取建筑列表
export function getBuildingArr(projectName = "") {
  let data = {
    projectName
  }
  return fetch({
    method: 'POST',
    url: `/projects?openId=${openId}`,
    data
  })
}
//获取报修地点列表
export function getRepairPlaceArr(projectId) {
  let data = {
    projectId
  }
  return fetch({
    method: 'POST',
    url: `/regionspace?openId=${openId}`,
    data
  })
}

