/*
  问卷调查的接口
*/
import fetch from 'utils/sagaCloudFetch'
import tools from 'utils/tools'
let openId = tools.getCookie('openId')
//模拟请求
export function mock() {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve(true)
    }, 1000);
  })
}
//获取调查问卷状态 1-进入userInfo ，2-进入part1，3-进入part2，,4-进入result
export function getQuestionnaireStatus() {
  let data = {
    openId
  }
  return fetch({
    method: 'POST',
    url: `/getQuestionnaireStatus`,
    data
  })
}
//添加调查问卷个人信息
export function addQuestionnaireUser({ userName, sex, age, address, post }) {
  let data = {
    openId,
    userName,
    sex,
    age,
    address,
    post
  }
  return fetch({
    method: 'POST',
    url: `/addQuestionnaireUser`,
    data
  })
}

//获取第一部分调查问卷内容 
export function getQuestionnaireList() {
  let data = {}
  return fetch({
    method: 'POST',
    url: `/getQuestionnaireList?openId=${openId}`,
    data
  })
}
//添加第一部分调查记录
export function addQuestionnaireList(data) {
  return fetch({
    method: 'POST',
    url: `/addQuestionnaireList?openId=${openId}`,
    data
  })
}
//第一部分调查结果report1
export function getQuestionnaireAnswer() {
  let data = {
    openId
  }
  return fetch({
    method: 'POST',
    url: `/getQuestionnaireAnswer`,
    data
  })
}
//第二部分添加记录
export function addSecQuestionnaireAnswer(data) {
  return fetch({
    method: 'POST',
    url: `/addSecQuestionnaireAnswer?openId=${openId}`,
    data
  })
}
//第二部分查询记录
export function getSecQuestionnaireAnswer() {
  let data = {};
  return fetch({
    method: 'POST',
    url: `/getSecQuestionnaireAnswer?openId=${openId}`,
    data
  })
}


